import { Grid } from '@mui/material';
import { Container } from "@mui/system";
import CardComponent from './CardComponent';

function Home() {
  const cardData = [
    {
      image: '/collision.png',
      title: 'Collision Simulation',
      link: '/collision'
    },
    {
      image: '/reversi.png',
      title: 'Reversi Game',
      link: '/reversi'
    },
    {
      image: '/typer.png',
      title: 'Typer',
      link: '/typer'
    },
  ];

  return (
    <Container>
      <Grid container spacing={2}>
        {cardData.map((card, index) => (
          <CardComponent
            key={index}
            image={card.image}
            title={card.title}
            link={card.link}
          />
        ))}
      </Grid>
    </Container>
  );
}

export default Home;
