import { lazy, Suspense } from "react";
import { Route, Routes } from "react-router-dom";
import Home from "./pages/Home";
import Loading from './pages/Loading';

const Collision = lazy(() => import("./pages/Collision/Collision"));
const Reversi = lazy(() => import("./pages/Reversi/Reversi"));
const Typer = lazy(() => import("./pages/Typer/Typer"));

function App() {
  return (
    <Suspense>
      <Routes path="/">
        <Route index element={<Home />} />
        <Route path="collision" element={LazyLoad(<Collision />)} />
        <Route path="reversi" element={LazyLoad(<Reversi />)} />
        <Route path="typer" element={LazyLoad(<Typer />)} />
      </Routes>
    </Suspense>
  );
}

function LazyLoad(component) {
  return <Suspense fallback={<Loading />}>
    {component}
  </Suspense>
}

export default App;
