import React from 'react';

const Loading = () => {
  return (
    <div className="loading-page">
      <div className="loading-spinner">
        <div className="bounce1"></div>
        <div className="bounce2"></div>
        <div className="bounce3"></div>
      </div>
      <h2>Loading...</h2>
    </div>
  );
};

export default Loading;